import React, { useEffect, useMemo, useState } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import styled from 'styled-components';
import BottomNavigation from '../components/BottomNavigation';
import { MessageSquare, Send, CheckCircle, ArrowLeft } from 'lucide-react';
import StatusTime from '../components/StatusTime';

const Container = styled.div`
  min-height: 100vh;
  background-color: #f5f5f5;
  padding-bottom: 80px;
`;

const Header = styled.div`
  background: white;
  padding: 12px 16px;
  display: flex;
  align-items: center;
  gap: 16px;
  border-bottom: 1px solid #e0e0e0;
  position: relative;
`;

const BackButton = styled.button`
  background: none;
  border: none;
  padding: 8px;
  cursor: pointer;
  color: #6366f1;
  display: flex;
  align-items: center;
  justify-content: center;
`;

const Title = styled.h1`
  font-size: 18px;
  font-weight: 600;
  color: #1f2937;
  flex: 1;
  text-align: center;
  margin-right: 40px;
`;

const StatusBar = styled.div`
  position: absolute;
  top: 12px;
  right: 16px;
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 14px;
  color: #6b7280;
`;

const Content = styled.div`
  padding: 24px 16px;
`;

const ChatSection = styled.div`
  background: white;
  border-radius: 8px;
  padding: 16px;
  display: flex;
  flex-direction: column;
  gap: 12px;
`;

const MessageRow = styled.div`
  display: flex;
  gap: 8px;
  align-items: flex-start;
`;

const MessageBubble = styled.div`
  max-width: 80%;
  padding: 10px 12px;
  border-radius: 12px;
  font-size: 14px;
  line-height: 1.5;
  ${props => props.role === 'assistant' ? `
    background: #f3f4f6;
    color: #374151;
    border-top-left-radius: 4px;
  ` : `
    background: #6366f1;
    color: white;
    margin-left: auto;
    border-top-right-radius: 4px;
  `}
`;

const InputRow = styled.div`
  display: flex;
  gap: 8px;
  margin-top: 8px;
`;

const TextInput = styled.input`
  flex: 1;
  padding: 12px;
  border-radius: 8px;
  border: 2px solid #e5e7eb;
  font-size: 14px;
`;

const SendButton = styled.button`
  padding: 12px 16px;
  border-radius: 8px;
  background: #6366f1;
  color: white;
  border: none;
  font-weight: 600;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 6px;
`;

const ActionButtons = styled.div`
  display: flex;
  flex-direction: column;
  gap: 12px;
  margin-top: 16px;
`;

const ActionButton = styled.button`
  width: 100%;
  padding: 16px;
  border-radius: 8px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  transition: all 0.2s;
  ${props => props.primary ? `
    background: #10b981;
    color: white;
    border: none;
    &:hover { background: #0ea571; }
  ` : `
    background: white;
    color: #374151;
    border: 2px solid #e5e7eb;
    &:hover { border-color: #6366f1; color: #6366f1; }
  `}
`;

const ChatbotPage = () => {
  const location = useLocation();
  const navigate = useNavigate();
  const initialData = location.state?.remittanceData || {};
  const [formData, setFormData] = useState(initialData);
  const [messages, setMessages] = useState([{
    role: 'assistant',
    text: '',
  }]);
  const [input, setInput] = useState('');
  const [currentField, setCurrentField] = useState(null);

  const requiredFields = useMemo(() => ([
    { key: 'senderName', prompt: 'Name of Sender*' },
    { key: 'address', prompt: 'Address' },
    { key: 'idNumber', prompt: 'ID/Passport No.' },
    { key: 'isResident', prompt: 'Resident-  Yes  No' },
    { key: 'contactNumber', prompt: 'Contact Telephone Number' },
    { key: 'paymentMethod', prompt: 'From AccountBy Cash Others' },
    { key: 'debitAcCurrency', prompt: 'Debit A/C Currency' },
    { key: 'debitAc', prompt: 'Debit A/C No.*' },
    { key: 'chargesDebitAcCurrency', prompt: 'Charges Debit A/C Currency' },
    { key: 'chargesDebitAc', prompt: 'Charges Debit A/C No.*' },
    { key: 'intermediaryBank', prompt: 'Intermediary Bank Name' },
    { key: 'beneficiaryBank', prompt: 'Beneficiary\'s Bank Name' },
    { key: 'beneficiaryName', prompt: 'Beneficiary Name' },
    { key: 'dealerName', prompt: 'Dealer\'s Name' },
    { key: 'fxRates', prompt: 'FX rates' },
  ]), []);

  const findNextMissing = () => {
    return requiredFields.find(f => !formData[f.key] || String(formData[f.key]).trim() === '');
  };

  useEffect(() => {
    // 
    const next = findNextMissing();
    if (next && next.key !== currentField) {
      setCurrentField(next.key);
      setMessages(m => [...m, { role: 'assistant', text: next.prompt }]);
    }
  }, [formData, currentField]);

  const handleSend = () => {
    if (!input.trim()) return;
    if (!currentField) return;

    // 
    setMessages(m => [...m, { role: 'user', text: input.trim() }]);
    
    // 
    let value = input.trim();
    
    // 
    if (currentField === 'isResident') {
      value = value.toLowerCase() === 'yes' ? 'Yes' : 'No';
    } else if (currentField === 'paymentMethod') {
      if (value.toLowerCase().includes('account') || value.toLowerCase().includes('')) {
        value = 'From Account';
      } else if (value.toLowerCase().includes('cash') || value.toLowerCase().includes('')) {
        value = 'By Cash';
      } else {
        value = 'Others';
      }
    }
    
    // 
    const updated = { ...formData, [currentField]: value };
    setFormData(updated);
    setInput('');

    //  useEffect 
    const next = requiredFields.find(f => !updated[f.key] || String(updated[f.key]).trim() === '');
    if (!next) {
      // 
      setMessages(m => [...m, { role: 'assistant', text: '""' }]);
      setCurrentField(null);
    }
  };

  const allFilled = requiredFields.every(f => formData[f.key] && String(formData[f.key]).trim() !== '');

  const handleConfirmAndDownload = () => {
    navigate('/download', { state: { remittanceData: formData, fileType: 'remittance', from: '/chatbot' } });
  };

  return (
    <Container>
      <Header>
        <BackButton onClick={() => navigate('/remittance-form', { state: { remittanceData: formData } })}>
          <ArrowLeft size={24} />
        </BackButton>
        <Title>Chatbot Assist</Title>
        <StatusBar>
          <StatusTime />
        </StatusBar>
      </Header>

      <Content>
        <ChatSection>
          <MessageRow>
            <MessageSquare size={20} style={{ color: '#6b7280' }} />
            <div style={{ fontSize: 14, color: '#6b7280' }}>
              REMITTANCE APPLICATION FORM
            </div>
          </MessageRow>

          {messages.map((msg, idx) => (
            <MessageRow key={idx}>
              <MessageBubble role={msg.role}>{msg.text}</MessageBubble>
            </MessageRow>
          ))}

          <InputRow>
            <TextInput
              placeholder={currentField ? '...' : ''}
              value={input}
              onChange={e => setInput(e.target.value)}
              disabled={!currentField}
            />
            <SendButton onClick={handleSend} disabled={!currentField}>
              <Send size={18} />
              
            </SendButton>
          </InputRow>
        </ChatSection>

        <ActionButtons>
          <ActionButton primary disabled={!allFilled} onClick={handleConfirmAndDownload}>
            <CheckCircle size={20} />
            {allFilled ? '' : ''}
          </ActionButton>
        </ActionButtons>
      </Content>

      <BottomNavigation />
    </Container>
  );
};

export default ChatbotPage;